@keyframes fade-in-animation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fade-in-animation1 {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
@keyframes fade-in-animation2 {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
.fade-in {
  animation: fade-in-animation 1s ease;
}
.fade-in1 {
  animation: fade-in-animation1 1s ease;
}
.fade-in2 {
  animation: fade-in-animation2 1s ease;
}

